 .gap{
  margin-top: 5vh!important;
}

.leftside .rightside{
    height:100%;
    width:100%;
}

.leftside {
    background:red;
}
.rightside {
    background:blue;
}



.slider {
    position:relative;
    display:flex;
    height:800px;
    justify-content: center;
    align-items:center;
}

.image {
    object-fit:cover;
    width: 800px;
    height: 600px;
    border-radius: 10px;

}
.right-arrow {
    position:absolute;
    top:45%;
    right:20%;
    font-size:3rem;
    color:green;
    z-index:10;
    cursor:pointer;
    user-select:none ;


}

.left-arrow {
    position:absolute;
    top:45%;
    left:20%;
    font-size:3rem;
    color:green;
    z-index:10;
    cursor:pointer;
    user-select:none;
    

}
.slide {
    opacity:0;
    transition-duration:1s ease;

}

.slide.active {
    opacity:1;
    transition-duration:1s;
    transform:scale(1.2);
}

:root {
    --bg:#242526;
    --bg-accent:#484a4d;
    --text-color:#dadce1;
